.Modal {
    color: #fff;
    position: fixed;
    top: 50%;
    left: 50%;
    justify-content: center;
    flex-direction: column;
    display: flex;
    padding: 40px;
    z-index: 3
}

.Modal--Generic {
    border: 1px solid #fff;
    background: #000;
    overflow-y: scroll
}

.Modal--Static {
    border: none;
    position: relative;
    margin: 0 auto;
    transform: none;
    left: unset;
    top: unset
}

.Modal-Background {
    background-color: rgba(0, 0, 0, .9);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3
}

.Modal-Close {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center
}

.Modal-Close svg {
    width: 100%;
    height: 100%
}

.Modal-Close:hover {
    border: 1px solid #fff;
    border-radius: 50%
}

.LoadingSpinner {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center
}

.LoadingSpinner-Inline svg {
    width: 100%
}

.LoadingSpinner-Inline svg, .LoadingSpinner svg {
    -webkit-animation-name: spin;
    animation-name: spin;
    -webkit-animation-duration: 1.5s;
    animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear
}

.LoadingSpinner svg {
    width: 50px;
    height: 50px
}

@-webkit-keyframes spin {
    0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(180deg)
    }
    to {
        transform: rotate(1turn)
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(180deg)
    }
    to {
        transform: rotate(1turn)
    }
}

