[type=range] {
    -webkit-appearance: none;
    background: transparent;
    margin: 10px 0;
    width: 100%
}

[type=range]::-moz-focus-outer {
    border: 0
}

[type=range]:focus {
    outline: 0
}

[type=range]:focus::-webkit-slider-runnable-track {
    background: #2b2b2b
}

[type=range]:focus::-ms-fill-lower {
    background: #1e1e1e
}

[type=range]:focus::-ms-fill-upper {
    background: #2b2b2b
}

[type=range]::-webkit-slider-runnable-track {
    cursor: default;
    height: 8px;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    width: 100%;
    box-shadow: 0 0 0 rgba(0, 0, 0, .2), 0 0 0 rgba(13, 13, 13, .2);
    background: #1e1e1e;
    border: 0 solid #656565;
    border-radius: 0
}

[type=range]::-webkit-slider-thumb {
    box-shadow: 0 0 0 rgba(0, 0, 0, .2), 0 0 0 rgba(13, 13, 13, .2);
    background: #656565;
    border: 0 solid #eceff1;
    border-radius: 10px;
    box-sizing: border-box;
    cursor: default;
    height: 20px;
    width: 20px;
    -webkit-appearance: none;
    margin-top: -6px
}

[type=range]::-moz-range-track {
    height: 8px;
    -moz-transition: all .2s ease;
    transition: all .2s ease;
    width: 100%;
    background: #1e1e1e;
    border: 0 solid #656565;
    border-radius: 0;
    height: 4px
}

[type=range]::-moz-range-thumb, [type=range]::-moz-range-track {
    box-shadow: 0 0 0 rgba(0, 0, 0, .2), 0 0 0 rgba(13, 13, 13, .2);
    cursor: default
}

[type=range]::-moz-range-thumb {
    background: #656565;
    border: 0 solid #eceff1;
    border-radius: 10px;
    box-sizing: border-box;
    height: 20px;
    width: 20px
}

[type=range]::-ms-track {
    cursor: default;
    height: 8px;
    -ms-transition: all .2s ease;
    transition: all .2s ease;
    width: 100%;
    background: transparent;
    border-color: transparent;
    border-width: 10px 0;
    color: transparent
}

[type=range]::-ms-fill-lower {
    background: #111
}

[type=range]::-ms-fill-lower, [type=range]::-ms-fill-upper {
    box-shadow: 0 0 0 rgba(0, 0, 0, .2), 0 0 0 rgba(13, 13, 13, .2);
    border: 0 solid #656565;
    border-radius: 0
}

[type=range]::-ms-fill-upper {
    background: #1e1e1e
}

[type=range]::-ms-thumb {
    box-shadow: 0 0 0 rgba(0, 0, 0, .2), 0 0 0 rgba(13, 13, 13, .2);
    background: #656565;
    border: 0 solid #eceff1;
    border-radius: 10px;
    box-sizing: border-box;
    cursor: default;
    height: 20px;
    width: 20px;
    margin-top: 2px
}

[type=range]:disabled::-moz-range-thumb, [type=range]:disabled::-ms-fill-lower, [type=range]:disabled::-ms-fill-upper, [type=range]:disabled::-ms-thumb, [type=range]:disabled::-webkit-slider-runnable-track, [type=range]:disabled::-webkit-slider-thumb {
    cursor: not-allowed
}

