.Peanut {
    display: none
}

.Peanut-Icon {
    width: 12px
}

.Peanut-Container {
    position: relative
}

.Peanut-Container, .Peanut-Line {
    display: flex;
    flex-direction: row
}

.Peanut-Eating {
    display: block;
    position: absolute;
    color: #fff;
    bottom: 8px;
    left: 17px
}

.Peanut-Eating svg {
    -webkit-animation-name: flyingPeanut;
    animation-name: flyingPeanut;
    -webkit-animation-duration: 2.5s;
    animation-duration: 2.5s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    -webkit-animation-direction: normal;
    animation-direction: normal
}

@-webkit-keyframes flyingPeanut {
    0% {
        transform: translateY(0) scaleX(1) rotate(0)
    }
    to {
        transform: translateY(-40px) scale3d(1.2, 1.2, 1.2) rotate(2turn)
    }
}

@keyframes flyingPeanut {
    0% {
        transform: translateY(0) scaleX(1) rotate(0)
    }
    to {
        transform: translateY(-40px) scale3d(1.2, 1.2, 1.2) rotate(2turn)
    }
}

